/* =============================in case if NOT login yet==================================================== */
@media screen and (min-width: 768px) {
  .backgroundHome {
    background: url(../../assets/png/backgroundTab_x1.png) no-repeat 100% 100%;
    background-size: 768px 563px;
  }
  .backgroundDiaryCalc {
    background: url(../../assets/png/backgroundDiaryTab_x1.png) no-repeat 100% 100%;
    background-size: 369px 338px;
  }
  .backgroundAuth {
    background: url(../../assets/png/backgroundTabAuth_x1.png) no-repeat 100% 100%;
    background-size: 752px 944px;
  }
  @media screen and (min-device-pixel-ratio: 2),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (-o-min-device-pixel-ratio: 2/1),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .backgroundHome {
      background-image: url(../../assets/png/backgroundTab_x2.png);
    }
    .backgroundDiaryCalc {
      background-image: url(../../assets/png/backgroundDiaryTab_x2.png);
    }
    .backgroundAuth {
      background: url(../../assets/png/backgroundTabAuth_x2.png) no-repeat 100% 100%;
    }
  }
}

@media screen and (min-width: 1280px) {
  .backgroundHome {
    background: url(../../assets/png/backgroundAuthDeskTop_x1.png) no-repeat 100% 0%;
    background-size: contain;
  }

  .backgroundDiaryCalc {
    background: url(../../assets/png/backgroundDiaryCalcDeskTop_x1.png) no-repeat 100% 0%;
    background-size: contain;
  }

  @media screen and (min-device-pixel-ratio: 2),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (-o-min-device-pixel-ratio: 2/1),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .backgroundHome {
      background-image: url(../../assets/png/backgroundAuthDeskTop_x2.png);
    }
    .backgroundDiaryCalc {
      background-image: url(../../assets/png/backgroundDiaryCalcDeskTop_x2.png);
    }
  }
}
